* {
  margin: 0;
  padding: 0;
  ::-webkit-scrollbar {
    display: none;
  }
}
li {
  list-style-type: none;
}

@import url("https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap");

.navbar {
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid #e5e5e5;
  .left {
    display: flex;
    .menu {
      padding: 15px;
      display: flex;
      align-items: center;
    }
    .logo {
      display: flex;
      align-items: center;
      img {
        width: 30px;
      }
      h5 {
        font-family: "Roboto";
        font-weight: 500;
        margin-left: 2px;
      }
    }
  }
  .middle {
    display: none;
  }
  .right {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    svg {
      margin-right: 10px;
    }
  }
}
.topics {
  display: flex;
  padding: 10px 0px 10px 10px;
  overflow-x: scroll;
  border-bottom: 1px solid #e5e5e5;

  .butt {
    display: flex;
    .buttons {
      padding: 5px 10px;
      color: black;
      border-radius: 16px;
      border: 1px solid gray;
      margin-right: 4px;
      background-color: rgb(230, 230, 230);
      cursor: pointer;
      h6 {
        font-size: 14px;
      }
    }
    .buttons:hover {
      background-color: #909090;
      transition: 100ms ease-in-out;
    }
  }

}

@media (min-width: 496px) {
  .navbar{
    .middle{
      display: unset;
    }
  }
}
