@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
*{margin:0;padding:0}* ::-webkit-scrollbar{display:none}li{list-style-type:none}.navbar{display:flex;justify-content:space-between;border-bottom:1px solid #e5e5e5}.navbar .left{display:flex}.navbar .left .menu{padding:15px;display:flex;align-items:center}.navbar .left .logo{display:flex;align-items:center}.navbar .left .logo img{width:30px}.navbar .left .logo h5{font-family:"Roboto";font-weight:500;margin-left:2px}.navbar .middle{display:none}.navbar .right{display:flex;align-items:center;justify-content:space-evenly}.navbar .right svg{margin-right:10px}.topics{display:flex;padding:10px 0px 10px 10px;overflow-x:scroll;border-bottom:1px solid #e5e5e5}.topics .butt{display:flex}.topics .butt .buttons{padding:5px 10px;color:#000;border-radius:16px;border:1px solid gray;margin-right:4px;background-color:#e6e6e6;cursor:pointer}.topics .butt .buttons h6{font-size:14px}.topics .butt .buttons:hover{background-color:#909090;transition:100ms ease-in-out}@media(min-width: 496px){.navbar .middle{display:unset}}
